@use "../../scss/abstracts";

.wp-block-md-pointcentral-fse-sample-block {
  background-color: abstracts.$green-theme-color;
  color: abstracts.$white-theme-color;
  padding: 5px 10px;
}

.md_quote_slider {
  position: relative;
  .md-quote-slider__items {
    max-width: 70%;
    margin: 0 auto;
  }
}

.md_quote_slider blockquote {
  padding: 0 25px;
  -ms-transition:
    transform 0.5s,
    opacity 0.5s;
  transition:
    transform 0.5s,
    opacity 0.5s;
  -webkit-transition:
    transform 0.5s,
    opacity 0.5s;
  text-align: center;
  color: inherit;
  background-image: none;
  font-size: 22px;
  width: 100%;
  margin: 0 auto;

  .testimonial-name {
    color: #225ba3;
    font-size: 18px !important;
    font-weight: 800;
    transform: scale(.8);
    -webkit-transform: scale(.8);
    display: block;
    line-height: 22px;
  }
}

.md_quote_slider blockquote.no-trans {
  -ms-transition: none;
  transition: none;
  -webkit-transition: none;
  opacity: 0;
  transform: translateX(-25px);
  z-index: 1;
  height: 0;
  margin: 0;
  padding: 0;
  p,
  span,
  div {
    margin: 0;
    height: 0;
    padding: 0;
    font-size: 0;
    display: block;
  }
}

.md_quote_slider .control-wrap {
  width: 20px;
  line-height: 20px;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
}

.md_quote_slider .controls .out-of,
.md_quote_slider .controls .total {
  display: inline-block;
  font-size: 16px;
  line-height: 20px;
  color: inherit;
  vertical-align: top;
}

.md_quote_slider .control-wrap {
  font-size: 16px;
}

.md_quote_slider .controls .out-of,
.md_quote_slider .controls .total {
  width: 20px;
  text-align: center;
}

.md_quote_slider .controls .out-of {
  width: 13px;
  top: -1px;
  position: relative;
}

.md_quote_slider .control-wrap ul {
  width: auto;
  -ms-transition: transform 0.33s;
  -webkit-transition: transform 0.33s;
  transition: transform 0.33s;
}

.md_quote_slider .control-wrap ul li {
  color: inherit;
  display: block;
  float: left;
  width: 20px;
  font-size: 16px;
  line-height: 20px;
  cursor: auto;
}

.md_quote_slider .controls {
  vertical-align: top;
}

.md_quote_slider .testimonial-next-prev .prev {
  position: absolute;
  top: 50%;
  height: 40px;
  width: 40px;
  font-size: 25px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  margin-top: -40px;
  left: 7.5%;
  margin-left: -8px;
  color: inherit;
  text-align: center;
}

.md_quote_slider .testimonial-next-prev .next {
  position: absolute;
  top: 50%;
  height: 40px;
  margin-top: -40px;
  font-size: 25px;
  width: 40px;
  margin-right: -8px;
  color: inherit;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  right: 7.5%;
  text-align: center;
}

.md_quote_slider .testimonial-next-prev .next:before,
.md_quote_slider .testimonial-next-prev .prev:before {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

.md_quote_slider .testimonial-next-prev .next:after,
.md_quote_slider .testimonial-next-prev .prev:after {
  backface-visibility: hidden;
  display: block;
  content: " ";
  z-index: 100;
  position: absolute;
  width: 30px;
  height: 2px;
  background-color: currentColor;
  top: 48%;
  opacity: 0;
  right: 3px;
  cursor: pointer;
  -ms-transform: translateY(-50%) scaleX(0) translateZ(0);
  transform: translateY(-50%) scaleX(0) translateZ(0);
  -webkit-transform: translateY(-50%) scaleX(0) translateZ(0);
  transition:
    opacity 0.5s cubic-bezier(0.2, 1, 0.2, 1),
    transform 0.5s cubic-bezier(0.2, 1, 0.2, 1);
  -webkit-transition:
    opacity 0.5s cubic-bezier(0.2, 1, 0.2, 1),
    -webkit-transform 0.5s cubic-bezier(0.2, 1, 0.2, 1);
}

.md_quote_slider .image-icon {
  background-size: contain;
  border-radius: 0 !important;
  background-repeat: no-repeat;
  width: 50px;
  height: 50px;
  border: none;
  text-indent: -9999px;
  margin: 0 auto;
}

.md_quote_slider p {
  font-size: 19px;
  line-height: 25px;
  font-family: Montserrat !important;
  font-weight: 500;
  padding-bottom: 30px;
  color: #545454;
  z-index: 9999;
  position: relative;
}

.md_quote_slider .testimonial-next-prev button {
  background: 0 0;
  border: none;
  cursor: pointer;
  color: #15487e !important;
  font-size: 50px !important;
}
